import $ from './lib/lib';
import "./lib/dynamic_adapt/script";
import scrolling from  "./lib/components/scrolling";
import modals from  "./lib/components/modals";
import "./swiper-sliders.js";
import "./lib/myFunctions/user-menu";
import "./lib/myFunctions/test";



window.addEventListener('DOMContentLoaded', () => {
    "use strict";

$('.header__burger').click(function(event) {
    $('.header__burger,.header__menu').toggleClass('active');
    $('body').toggleClass('lock');
});

function ibg() {

    let ibg = document.querySelectorAll(".ibg");
    for (var i = 0; i < ibg.length; i++) {
        if (ibg[i].querySelector('img')) {
            ibg[i].style.backgroundImage = 'url(' + ibg[i].querySelector('img').getAttribute('src') + ')';
        }
    }
}

ibg();

scrolling('.pageup');
modals();

});

